.navbar {
  grid-column: 6 / 12;
  text-align: center;
}

.link {
  margin: 0 1vw;
  color: #d6d6d6;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.header {
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  background: #404040;
  color: #d6d6d6;
  z-index: 5;
  position: fixed;
  height: 60px;
  top: 0;
  -webkit-align-items: end;
          align-items: end;
}

.logo {
  height: 40px;
  grid-area: 1 / 1;
  align-self: start;
}

.logo img {
  height: 100%;
}

.footer {
  height: 120px;
  background: #404040;
  position: absolute;
  width: 100vw;
  z-index: 5;
  color: #d6d6d6;
  text-align: center;
  display: grid;
  grid-template-columns: 20% 1fr 20%;
  grid-template-rows: auto;
  -webkit-align-items: center;
          align-items: center;
}

.logo {
  width: 28px;
  height: 28px;
  margin: 1vh 1vw;
}

a {
  color: #d6d6d6;
  justify-self: start;
  grid-area: 1/ 1;
}

.footer h5 {
  grid-area: 1 / 2;
}

#reference {
  grid-area: 2/ 2;
  font-size: 11px;
}

.full-pic {
  width: 100vw;
  grid-row-start: 3;
}

.full-pic img {
  width: 100%;
}

.snippet-container {
  position: relative;
  z-index: 2;
  background: white;
  width: 100vw;
  padding: 0 4vw 6vh 4vw;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-area: 2 / 1 / 3 / 5;
}

.snippet {
  width: 15vw;
  z-index: 1;
  position: relative;
  display: grid;
  justify-items: center;
}

.snippet img {
  width: 100%;
  -webkit-filter: brightness(0.75);
          filter: brightness(0.75);
  z-index: 1;
}

.onhover {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #404040;
  opacity: 0;
  z-index: 5;
}

.snippet:hover .onhover {
  cursor: pointer;
  opacity: 0.8;
}

.snippet:hover h2 {
  cursor: pointer;
}

.snippet:hover .snippet-title {
  text-decoration: underline;
}

.snippet-title {
  position: absolute;
  top: 50%;
  z-index: 6;
  color: white;
}

.static-image {
  width: 100vw;
  position: fixed;
  top: 0;
  z-index: 1;
  -webkit-filter: brightness(0.7);
          filter: brightness(0.7);
}

.title {
  color: #d6d6d6;
  z-index: 2;
  position: fixed;
  font-size: 55px;
  opacity: 0;
}

@-webkit-keyframes slideInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  1% {
    opacity: 1;
  }
  95% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  1% {
    opacity: 1;
  }
  95% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

#main-title-1 {
  -webkit-animation: 0.4s linear 0s 1 slideInFromBottom;
          animation: 0.4s linear 0s 1 slideInFromBottom;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  left: 55%;
  top: 25%;
}

.hide {
  opacity: 0;
  color: transparent;
}

.show {
  opacity: 1;
}

#main-title-2 {
  -webkit-animation: 0.4s linear 0.7s 1 slideInFromBottom;
          animation: 0.4s linear 0.7s 1 slideInFromBottom;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  left: 60%;
  top: 35%;
}

p {
  word-wrap: break-word;
  color: black;
  position: relative;
  z-index: 3;
  grid-area: 1 / 2;
  padding: 4vh 0;
}

.body-container {
  position: relative;
  z-index: 2;
  background: white;
  width: 100vw;
  display: grid;
  grid-template-columns: 20% 1fr 20%;
  text-align: center;
}

.paragraph-start {
  font-weight: bold;
}

.promotional {
  height: 100vh;
  width: 100vw;
  background: transparent;
}

.about-main-pic {
  width: 100vw;
}

.about-body-container {
  position: relative;
  margin: -30vh 30vw;
  width: 70vw;
  display: grid;
  background-color: white;
  grid-template-rows: repeat(7, 1fr);
  grid-template-columns: 30% 1fr;
  grid-auto-rows: 30px;
}

.p-container {
  grid-area: 2 / 2 / 7 / 3;
}

.about-title {
  grid-column-start: 2;
  justify-self: center;
  align-self: center;
}

.p-container p {
  text-indent: 50px;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.hover-bike {
  width: 100%;
}

.hovering-bike-container {
  width: 500px;
  position: relative;
}

.wheel {
  background: green;
  opacity: 0.5;
  width: 184px;
  height: 185px;
  border-radius: 50%;
  position: absolute;
  top: 119px;
  left: 18px;
}

.wheel2 {
  background: green;
  opacity: 0.5;
  width: 184px;
  height: 185px;
  border-radius: 50%;
  position: absolute;
  top: 119px;
  right: 18px;
}

.fork {
  background: red;
  opacity: 0.5;
  height: 89px;
  width: 17px;
  position: absolute;
  top: 121px;
  right: 125px;
  -webkit-transform: rotate(-29deg);
  transform: rotate(-29deg);
}

.head-tube {
  background: blue;
  opacity: 0.5;
  height: 57px;
  width: 21px;
  position: absolute;
  top: 62px;
  right: 149px;
  -webkit-transform: rotate(-29deg);
  transform: rotate(-18deg);
}

.crank {
  background: purple;
  opacity: 0.5;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  position: absolute;
  top: 208px;
  right: 245px;
}

.handle-bar {
  background: yellow;
  opacity: 0.5;
  height: 59px;
  width: 26px;
  position: absolute;
  top: 18px;
  right: 123px;
  -webkit-transform: rotate(-29deg);
  transform: rotate(89deg);
}

.cassette {
  background: orange;
  opacity: 0.5;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  position: absolute;
  top: 181px;
  right: 361px;
}

.chain1,
.chain2 {
  background: greenyellow;
  opacity: 0.5;
  height: 89px;
  width: 9px;
  position: absolute;
}

.chain1 {
  -webkit-transform: rotate(108deg);
          transform: rotate(108deg);
  top: 196px;
  right: 333px;
}

.chain2 {
  -webkit-transform: rotate(95deg);
          transform: rotate(95deg);
  top: 163px;
  right: 327px;
}

.service-display-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0 5vw;
}

.service-title-container {
  margin: 60px 0 30px 0;
  width: 100vw;
  display: grid;
  grid-template-columns: 25% 1fr 25%;
  text-align: center;
}

.service-title {
  grid-column-start: 2;
}

.star-list {
  list-style: none;
  margin: 30px;
}

.star::before {
  content: '* ';
}

.service-display {
  border: 1px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
}

.service-button {
  border: 1px solid #404040;
  background: #404040;
  color: #d6d6d6;
  width: 80px;
  height: 65px;
  border-radius: 5px;
  margin: 5px;
}

.service-button:hover {
  color: #404040;
  background: #d6d6d6;
}

.service-button:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.button-title {
  display: block;
  font-weight: normal;
  font-size: 18px;
}

.display-grid-container {
  display: -webkit-flex;
  display: flex;
  margin: 10vh 10vw;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
label,
input,
a {
  font-family: 'Ubuntu Condensed', sans-serif;
}

